

























import {Component, Vue, Watch} from 'vue-property-decorator';
import {DiamondUtil} from "../../util/diamond_util";
import {ToastUtil} from "../../util/toast";
import {
    Overlay,
    Loading,
    Icon
} from 'vant';
import 'vant/lib/overlay/style';
import 'vant/lib/loading/style';
import 'vant/lib/icon/style';
import {AuthAdminModel} from "../../model/auth_admin_model";

Vue.use(Overlay).use(Loading).use(Icon);

@Component({})

export default class AuthAdminRecord extends Vue {
    private bodyHeight = 0;
    private isLoadingAccount = true;
    private loadingHintInfo = "数据加载中...";
    private recordResult: AuthAdminModel[] = [];

    public created(): void {
        this.bodyHeight = document.documentElement.clientHeight * 93 / 100;
        this.$store.state.title = this.$t('authRecord');
    }

    public async mounted() {
        try {
            console.log("enter loadAuthAdminRecord===>");
            //let nativeAddress = "5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY";               //todo remove test account
            this.recordResult = await DiamondUtil.getInstance().loadAuthAdminRecord("5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY");
            console.log("result========>" + this.recordResult.toString());
            this.isLoadingAccount = false;
        } catch (e) {
            console.log("loadAuthAdminRecord error is ===>" + e.toString());
            this.isLoadingAccount = false;
            ToastUtil.fail("加载链上数据出现错误" + e.toString());
            return;
        }
    }

    public clickRecordDetail(item: AuthAdminModel) {
        this.$store.state.nowAuthAdminRecord = item;
        this.$router.push({name: 'auth_admin_detail'});
    }
}
